.pagination {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.pagination__link {
  display: inline-block;
  line-height: rem(22);
  padding: 0 rem(12);
  color: #ffffff;
  border: 1px solid #ffffff;
  transition: all .3s ease;
}

.pagination__link--previous {
}

.pagination__page {
  display: inline-block;
  padding: rem(2) rem(6) rem(4) rem(7);
  border: 1px solid #ffffff;
  line-height: rem(16);
  // margin-right: rem(16);
  transition: all .3s ease;
}

.pagination__page:hover,
.pagination__link:hover {
  color: #333333;
  background: #ffffff;
}

.pagination__page--smaller {
}

.pagination__page--current {
  color: #333333;
  background: #ffffff;
}

.pagination__page--larger {
}

.pagination__link--next {
}
